<template>
  <div class="search">
    <el-form style="text-align:left;padding: 20px">
      <el-form-item label="性别：">
        <el-checkbox-group v-model="selectGender">
          <el-checkbox-button v-for="item in genders"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="职业：">
        <el-checkbox-group v-model="selectCareer">
          <el-checkbox-button v-for="item in careers"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="标签：">
        <el-checkbox-group v-model="selectTag">
          <el-checkbox-button v-for="item in tags"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   @click="onClear">清空</el-button>
      </el-form-item>
    </el-form>
    <el-table :data="list"
              style="width:100%;padding: 20px">
      <el-table-column type="index">
      </el-table-column>
      <el-table-column prop="count"
                       label="满足条件数">
      </el-table-column>
      <el-table-column prop="name"
                       label="姓名">
      </el-table-column>
      <el-table-column prop="gender"
                       label="性别">
      </el-table-column>
      <el-table-column prop="career"
                       label="职业">
      </el-table-column>
      <el-table-column prop="level"
                       label="星级">
      </el-table-column>
      <el-table-column prop="tag"
                       label="标签">
      </el-table-column>

      <el-table-column prop="acquisition"
                       label="获取途径">
      </el-table-column>

      <el-table-column prop="camp"
                       label="阵营">
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { findAll } from '../api/role.js'
import { careers, camps, genders, tags, acquisitions } from '../utils/constant.js'
export default {
  name: 'role',
  data () {
    return {
      careers: careers(),
      tags: tags(),
      genders: genders(),
      selectCareer: [],
      selectTag: [],
      selectGender: [],
      roleList: []
    }
  },
  computed: {
    list () {
      let _list = this.roleList.map((item) => {
        let obj = { count: 0, ...item }
        this.selectCareer.forEach(per => {
          if (item.career && item.career.includes(per)) {
            obj.count++
          }
        })
        this.selectTag.forEach(per => {
          if (item.tag && item.tag.includes(per)) {
            obj.count++
          }
        })
        this.selectGender.forEach(per => {
          if (item.gender && item.gender.includes(per)) {
            obj.count++
          }
        })

        if (obj.tag) {
          if (typeof obj.tag === 'string') {
            obj.tag = [obj.tag]
          }
          obj.tag = obj.tag.map(per => {
            let _tag = this.tags.find(sub => {
              return sub.value === per
            }) || {}
            return _tag.label
          }).join('、')
        }

        if (obj.acquisition) {
          if (typeof obj.acquisition === 'string') {
            obj.acquisition = [obj.acquisition]
          }
          obj.acquisition = obj.acquisition.map(per => {
            let _acquisition = acquisitions().find(sub => {
              return sub.value === per
            }) || {}
            return _acquisition.label
          }).join('、')
        }

        if (obj.camp) {
          if (typeof obj.camp === 'string') {
            obj.camp = [obj.camp]
          }
          obj.camp = obj.camp.map(per => {
            let _camp = camps().find(sub => {
              return sub.value === per
            }) || {}
            return _camp.label
          }).join('、')
        }

        if (obj.career) {
          if (typeof obj.career === 'string') {
            obj.career = [obj.career]
          }
          obj.career = obj.career.map(per => {
            let _career = careers().find(sub => {
              return sub.value === per
            }) || {}
            return _career.label
          }).join('、')
        }
        if (obj.gender) {
          if (typeof obj.gender === 'string') {
            obj.gender = [obj.gender]
          }
          obj.gender = obj.gender.map(per => {
            let _gender = genders().find(sub => {
              return sub.value === per
            }) || {}
            return _gender.label
          }).join('、')
        }
        return obj
      }).filter(item => {
        return item.count > 0
      }) || []
      _list.sort((a, b) => {
        return b.count - a.count || b.level - a.level
      })
      return _list
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      findAll().then(res => {
        this.roleList = res
        // console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    onClear () {
      this.selectCareer = []
      this.selectTag = []
      this.selectGender = []
    }
  }
}
</script>
